.information{
    
    
    @include desktop{
        position: absolute;
        max-width: 800px;
        left: 40rem;
    }
}
.about{
    border-bottom: 3px solid rgba(250, 250, 250, 0.25);
    padding: 8rem 4.5rem;

    p{
        text-align: justify;
    }
}
.logros{
    margin-top: 8rem;
}

.logro{

    
    @include grid(1,0);
    p{
        margin: 3rem 0;
    }
    
    @include tablet{
        @include grid(2,5rem);
        p{
            padding-top: 5rem;
        }

        .mty{
            grid-column: 3 / 2;
        }
        .mty-text{
            grid-row: 2 / 3
        }
    }

    @include desktop{
        @include grid(2,5rem);
        
    }

    
}