.contact{
    margin-top: 8rem;
    padding-bottom: 8rem;
}

.form{
    margin-top: 5rem;
    width: 100%;
}

.form{

    
    text-align: center;
    input{
        background-color: rgba(250,250,250,0.1);
        border: 1px solid rgba(250,250,250,1);
        border-radius: 0px;
        margin-bottom: 3rem;
        color: #fff;
        font-size: 1.4rem;
        height: 4rem;
        line-height: 4rem;
        width: 100%;
    }

    textarea{
        background-color: rgba(250,250,250,0.1);
        border: 1px solid rgba(250,250,250,1);
        border-radius: 0px;
        margin-bottom: 3rem;
        color: #fff;
        font-size: 1.4rem;
        height: 18rem;
        max-height: 22rem;
        line-height: 4rem;
        width: 100%;
    }

    input[type=submit]{
        width: auto;
        padding: 0 20px;
        background-color: #fff;
        color: #a43f49;
        font-size: 12px;
        font-weight: 800;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        transition: all 0.5s;
        border: none;
        outline: none;
        cursor: pointer;

        &:hover{
            background-color: $rojo;
            color: $blanco;
        }
    }

}

