.skill-section{
    margin-top: 8rem;
    padding-bottom: 5rem;
    border-bottom: 3px solid rgba(250, 250, 250, 0.25);
}
.skills{
    @include grid(1,0);

    @include tablet{
        @include grid(2,5rem);
    }

    @include desktop{
        @include grid(2,5rem);
    }
}
.skill{
    text-align: center;
    margin: 1rem 0;
    img{
        width: 60px;
    }
}