.proyects{
    margin-top: 8rem;
    padding-bottom: 5rem;
    border-bottom: 3px solid hsla(0,0%,98%,.25);

}

.buttons-container{
    margin-top: 2rem;
    text-align: center;
    button{
        border: none;
        outline: none;
        cursor: pointer;
        padding: 1rem;
        color: $blanco;
        background-color: transparent;

        &:hover{
            color: $rojo;
        }

        &.style{
            color: $negro;
            background-color: $blanco;
            border-radius: 5px;
        }
    }
}

