.profile{
    
    display: none;    
    position: fixed;
    max-height: 100%;
    overflow-y: auto;
    overflow: hidden; 
    &:-webkit-scrollbar {display: none;}
    
    @include desktop{
        display: block;
    }
}

.info{
    
    max-width: 30rem;
    background-color: $rojo;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    div{
        text-align: center;
    }
}

.info__author{  
    margin-top: 1rem;
    h4{     
        margin-top: 0;
        font-size: 2.6rem;   
        color: $blanco;
    }
}
.img__author{
    padding: 4rem 8rem 4rem 8rem;
    img{
        border: 2px solid grey;
        margin: 0;
        padding: 0;
        border-radius: 80rem;
        overflow: hidden;
    }
}

.main-menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    a{
        font-size: 1.6rem;
        color: $blanco;
        padding: 1rem .5rem;
        border-bottom: 2px solid $blanco;

        &:hover{
            color: $negro;
            background-color: $blanco;
        }
    }
    
}

.main-menu-active{

    
    a{
        color: $negro;
        background-color: $blanco;
    }
}

.social-network{
    margin: 5rem 0;
    ul{
        display: flex;
        justify-content: space-between;
        gap: 0.5rem;
    }
}

.copyright-text{
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(250, 250, 250, 0.1);
}