.nav-toggle{
    color: $rojo;
    top: 1rem;
    background: $blanco;
    cursor: pointer;
    display: block;
    position: fixed;
    border-radius: 5px;   
    @include desktop{
        display: none;
    }
}



.nav-activado{
    display: block;       
    height: calc(100% - 1rem);
    overflow-y: auto;
    position: fixed; 
    z-index: 1;
    top: 7rem;
    left: 1rem;
    
    transition: .3s;
    @include desktop{
        display: none;
    }

}