html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
}
p {
    color: $blanco;
    font-size: 1.5rem;
    margin: 0;
    font-weight: $regular;
    letter-spacing: 0.5px;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;

}

ul{
    list-style: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
}

img {
    max-width: 100%;
}

h1, h2, h3 {
    font-family: $fuente_principal;
    margin: 0;
    font-weight: $bold;
    color: $blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
    font-weight: $bold;
}
h3 {
    font-size: 3rem;
    text-align: center;
}